      <div class="col-12 col-sm-auto mb-4 whois">
        <div class="cheader px-3 py-1">{{ addDollar(cmd.whois) }}</div>
        <div class="cbody px-3 py-2">{{ whois_msg }}</div>
      </div>
      <div class="col-12 mb-4 hobby">
        <div class="cheader px-3 py-1">{{ addDollar(cmd.hobby) }}</div>
        <div class="cbody px-3 pt-2">
          <div class="row">
            <div class="col-12 col-md-6 mb-2" v-for="(hobby, key) in hobbies" :key="`hobbies-${key}`">{{ addDot(hobby) }}</div>
          </div>
        </div>
      </div>
      <div class="col-12 mb-4 interest">
        <div class="cheader px-3 py-1">{{ addDollar(cmd.interest) }}</div>
        <div class="cbody px-3 pt-2">
          <div class="row">
            <div class="col-6 col-md-3 col-lg-2 mb-2" v-for="(interest, key) in interests" :key="`interests-${key}`">{{ addDot(interest) }}</div>
          </div>
        </div>
      </div>
      <div class="col-12 mb-4 skill">
        <div class="cheader px-3 py-1">{{ addDollar(cmd.skills) }}</div>
        <div class="cbody px-3 py-2">
          <div class="row">
            <div class="col-12 col-md-6 col-lg-4 mb-2" v-for="(skill, key) in skills" :key="`skills-${key}`">
              <div class="row">
                <div class="col-7">{{ addDot(skill.name) }}</div>
                <div class="col-5">{{ setStar(skill.star) }}</div>
              </div>
            </div>
            <div class="col-12">
              <hr />
            </div>
            <div class="col-12 col-md-6 col-lg-4 mb-2" v-for="(tool, key) in tools" :key="`tools-${key}`">
              <div class="row">
                <div class="col-7">
                  {{ addDot(tool.name) }}
                  <span class="reference" v-if="tool['reference']">{{ getIndex(tool.name) }}</span>
                </div>
                <div class="col-5">
                  {{ setStar(tool.star) }}
                </div>
              </div>
            </div>
            <div class="col-12 mb-1 reference text-right" v-for="(value, key ,index) in references" :key="`references-${index}`">
              [{{ index+1 }}] {{ key }}: {{ value }}
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  title() {
    return "AboutMe";
  },
  data() {
    return {
      name: "home",
      cmd: {
        whois: "whois",
        hobby: "hobby",
        interest: "interest",
        skills: "skills",
      },
      whois_msg: "Second-year master's student at Kagoshima University.",
      hobbies: [
        "Motorcycle Touring [ Ninja250 (2013) ]",
        "Smart House [ Raspberry Pi (IoT) ]",
      ],
      interests: [
        "Network",
        "Front-End",
        "Back-End",
        "Seucurity",
        "Darknet",
        "Honeypot",
      ],
      skills: [
        {
          name: "Ruby on Rails",
          star: 4,
        },
        {
          name: "Ruby",
          star: 4,
        },
        {
          name: "HTML",
          star: 4,
        },
        {
          name: "JavaScript",
          star: 4,
        },
        {
          name: "CSS",
          star: 4,
        },
        {
          name: "Bash",
          star: 3,
        },
        {
          name: "Vue.js",
          star: 3,
        },
        {
          name: "Python",
          star: 3,
        },
        {
          name: "Swift",
          star: 2,
        },
        {
          name: "Flutter",
          star: 1,
        },
        {
          name: "C",
          star: 1,
        },
      ],
      tools: [
        {
          name: "Git",
          star: 3,
          reference: false,
        },
        {
          name: "WordPress",
          star: 3,
          reference: false,
        },
        {
          name: "GAS",
          star: 3,
          reference: true,
        },
      ],
      references: {
        GAS: "Google Apps Script"
      },
    };
  },
  methods: {
    setStar: function(star_num) {
      var star = "★".repeat(star_num)
      star = star.padStart(5, "☆").split("").reverse().join("")
      return star
    },
    getIndex: function(key) {
      var index = Object.keys(this.references).indexOf(key) + 1
      return "[" + index + "]"
    }
  },
};
</script>

