          class: this.iconClass("fa-envelope"),
          style: "",
        },
      ]
    };
  },
  methods: {
    iconClass: function(fa_class) {
      var class_name = "";
      if (fa_class == "fa-envelope") {
        class_name += "fas "
      } else {
        class_name += "fab "
      }
      class_name += fa_class
      class_name += " "
      class_name += "fa-fw fa-2x faa-spin animated-hover mr-3"
      return class_name
    }
  },
}
</script>

