import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home'
import AboutMe from '@/views/AboutMe'
import Products from '@/views/Products'
import Activity from '@/views/Activity'
import Pair from '@/views/products/Pair'
import Accelerometer from '@/views/products/Accelerometer'
import Locater from '@/views/products/Locater'
import Dice from '@/views/products/Dice'
import RandomPrefecture from '@/views/products/RandomPrefecture'
import TravelingTool from '@/views/products/TravelingTool'
import NumGame from '@/views/products/NumGame'
import LTOrder from '@/views/products/LTOrder'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [{
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/aboutme',
      name: 'AboutMe',
      component: AboutMe
    },
    {
      path: '/products',
      name: 'Products',
      component: Products,
      children: [
        {
          path: 'pair',
          name: 'Pair',
          component: Pair
        },
        {
          path: 'accelerometer',
          name: 'Accelerometer',
          component: Accelerometer
        },
        {
          path: 'locater',
          name: 'Locater',
          component: Locater
        },
        {
          path: 'dice',
          name: 'Dice',
          component: Dice
        },
        {
          path: 'randomprefecture',
          name: 'RandomPrefecture',
          component: RandomPrefecture
        },
        {
          path: 'travelingtool',
          name: 'TravelingTool',
          component: TravelingTool
        },
        {
          path: 'numgame',
          name: 'NumGame',
          component: NumGame
        },
        {
          path: 'ltorder',
          name: 'LTOrder',
          component: LTOrder
        },
      ],
    },

    {
      path: '/activity',
      name: 'Activity',
      component: Activity
    },
    {
      path: '/notfound',
      component: Home
    },
    {
      path: '*',
      redirect: '/notfound'
    },
  ]
})